import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, FlatList, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      name: '',
      gender: 'F',
      countryCode: '65',
      phone: '',
      emailAddr: '',
      postalCode: '',
      address1: '',
      address2: '',
      address3: '',
      address4: '',
      birthday: '1964-05-08',
    };
  }

  async componentDidMount() {
    let session = await AsyncStorage.getItem('session');
    this.setState({
      session: session ? JSON.parse(session) : null,
    })
  }

  doPressGender() {
    this.setState({
      gender: this.state.gender === 'M' ? 'F' : 'M'
    })
  }

  doPostalCode() {
    const { postalCode } = this.state;
    let service = entry['fragrance'].service;
    let url = service + 'epod/api/epbsggeo';
    let params = new URLSearchParams();
    params.append('postal', postalCode);
    url += ('?' + params);

    fetch(url, {
      method: 'GET'
    })
      .then(response => response.json())
      .then(response => {
        if (response?.address) {
          this.setState({
            address2: response.address || '',
          });
        }
      })
  }

  doSubmit() {
    const { navigation, route } = this.props;
    const { name, gender, countryCode, phone, emailAddr, postalCode, address1, address2, birthday } = this.state;
    let service = entry['fragrance'].service;

    this.setState({
      loading: true
    }, () => {

      fetch(service + 'epod/api/create-vip', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify({
          orgId: entry['fragrance'].orgId,
          locId: entry['fragrance'].locId,
          userId: '',
          name: name,
          gender: gender,
          phone: '+' + countryCode + phone,
          emailAddr: emailAddr,
          postalCode: postalCode,
          address1: address1,
          address2: address2,
          dobDay: '8',
          dobMonth: '5',
          dobYear: '1954',
        }),
      }).then(response => response.json())
        .then(response => {
          console.log(response)
          this.setState({
            loading: false,
          }, () => navigation.navigate('Error', { message: response.message || 'VIP creation successful. We will send a message shortly to confirm your registration.' }))
        })

    })
  }

  render() {
    const { navigation, route, } = this.props;
    const { loading, session,
      name, gender, countryCode, phone, emailAddr, postalCode, address1, address2, address3, address4,
      birthday } = this.state;

    return (
      <View style={{ backgroundColor: 'white', }}>
        <View style={{ height: 60, margin: 10, flexDirection: 'row' }}>
          <Image
            style={{ height: 50, width: 150, }}
            source={{
              uri: entry['fragrance']?.logo
            }}
            resizeMode='contain'
          />
          <View style={{ flex: 1 }}></View>
        </View>
        <Text style={{ fontFamily: 'demi', fontSize: 20, marginHorizontal: 10, }}>My Registration <Text style={{ color: entry['fragrance'].theme }}>会员登记</Text></Text>
        <Text style={{ fontFamily: 'regular', marginHorizontal: 10, }}>Join us now for exclusive member deals and promos!</Text>

        <View style={{ margin: 10 }}>
          <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Name <Text style={{ color: entry['fragrance'].theme }}>姓名</Text></Text>
          <TextInput
            style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
            blurOnSubmit
            selectTextOnFocus={true}
            onChangeText={(text) => this.setState({ name: text })}
            value={name}
          />
        </View>

        <View style={{ flexDirection: 'row' }}>
          <View style={{ margin: 10, flex: 1 }}>
            <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Birthday <Text style={{ color: entry['fragrance'].theme }}>生日</Text></Text>
            <TextInput
              style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
              blurOnSubmit
              selectTextOnFocus={true}
              onChangeText={(text) => this.setState({ birthday: text })}
              value={birthday}
            />
          </View>
          <View style={{ margin: 10, flex: 1, }}>
            <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Gender <Text style={{ color: entry['fragrance'].theme }}>性别</Text></Text>
            <TouchableOpacity style={{ marginTop: 5, borderRadius: 5, minHeight: 50, padding: 10, justifyContent: 'center' }}
              onPress={() => this.doPressGender()}>
              <Text><Text style={{ fontFamily: 'demi', fontSize: 16, opacity: gender === 'M' ? 1 : 0.2 }}>Male <Text style={{ color: entry['fragrance'].theme }}>公</Text></Text>&ensp;/&ensp;<Text style={{ fontFamily: 'demi', fontSize: 16, opacity: gender === 'F' ? 1 : 0.2 }}>Female <Text style={{ color: entry['fragrance'].theme }}>母</Text></Text></Text>
            </TouchableOpacity>
          </View>
        </View>

        <View style={{ margin: 10 }}>
          <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Email <Text style={{ color: entry['fragrance'].theme }}>电邮</Text></Text>
          <TextInput
            style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
            blurOnSubmit
            selectTextOnFocus={true}
            keyboardType='email-address'
            onChangeText={(text) => this.setState({ emailAddr: text })}
            value={emailAddr}
          />
        </View>

        <View style={{ flexDirection: 'row' }}>
          <View style={{ margin: 10, flex: 1 }}>
            <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Phone <Text style={{ color: entry['fragrance'].theme }}>电话</Text></Text>
            <View style={{ flexDirection: 'row', width: '100%' }}>
              <View style={{ width: 50, marginRight: 5 }}>
                <TextInput
                  style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, textAlign: 'center' }}
                  blurOnSubmit
                  selectTextOnFocus={true}
                  keyboardType='number-pad'
                  onChangeText={(text) => this.setState({ countryCode: text })}
                  value={countryCode}
                />
              </View>
              <View style={{ flex: 1, marginLeft: 5 }}>
                <TextInput
                  style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
                  blurOnSubmit
                  selectTextOnFocus={true}
                  keyboardType='number-pad'
                  onChangeText={(text) => this.setState({ phone: text })}
                  value={phone}
                />
              </View>
            </View>
          </View>
          <View style={{ margin: 10, flex: 1, }}>
            <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Postal Code <Text style={{ color: entry['fragrance'].theme }}>邮编</Text></Text>
            <TextInput
              style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
              blurOnSubmit
              onBlur={() => this.doPostalCode()}
              selectTextOnFocus={true}
              keyboardType='number-pad'
              onChangeText={(text) => this.setState({ postalCode: text })}
              value={postalCode}
            />
          </View>
        </View>

        <View style={{ margin: 10 }}>
          <Text style={{ fontFamily: 'demi', letterSpacing: 1, fontSize: 12, marginTop: 5, textTransform: 'uppercase' }}>Address <Text style={{ color: entry['fragrance'].theme }}>地址</Text></Text>
          <TextInput
            style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
            blurOnSubmit
            selectTextOnFocus={true}
            onChangeText={(text) => this.setState({ address1: text })}
            value={address1}
            placeholder='Unit No. 门牌'
            placeholderTextColor={'rgba(0,0,0,0.2)'}
          />
          <TextInput
            style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
            blurOnSubmit
            selectTextOnFocus={true}
            onChangeText={(text) => this.setState({ address2: text })}
            value={address2}
          />
          {/* <TextInput
            style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
            blurOnSubmit
            selectTextOnFocus={true}
            onChangeText={(text) => this.setState({ address3: text })}
            value={address3}
          /> */}
          {/* <TextInput
            style={{ marginTop: 5, borderRadius: 5, backgroundColor: entry['fragrance'].theme1, minHeight: 50, padding: 10, fontFamily: 'demi', fontSize: 16, }}
            blurOnSubmit
            selectTextOnFocus={true}
            onChangeText={(text) => this.setState({ name: text })}
            value={name}
          /> */}
        </View>


        <TouchableOpacity style={{ marginTop: 30, marginHorizontal: 10, height: 50, backgroundColor: entry['fragrance'].theme, borderRadius: 5, justifyContent: 'center', alignItems: 'center' }}
          disabled={loading}
          onPress={() => this.doSubmit()}>
          <Text style={{ fontFamily: 'demi', fontSize: 16, letterSpacing: 1, color: 'white', textTransform: 'uppercase' }}>Join Now 加入会员</Text>
        </TouchableOpacity>
        <Text style={{ fontFamily: 'regular', marginVertical: 5, fontSize: 10, marginHorizontal: 10, textAlign: 'center' }}>&copy; 2023 Fragrance Foodstuff Pte Ltd</Text>
      </View>
    );
  }

}

const styles = StyleSheet.create({
});
