import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, FlatList, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import moment from 'moment';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { LinearGradient } from 'expo-linear-gradient';
import QRCode from 'react-native-qrcode-svg';
import temp1 from '../assets/temp1.png';

export default class Vip extends Component {
  constructor(props) {
    super(props);
    this.state = {
      wabaVipInfo: null,
      wabaSvInfo: [],
    };
  }

  async componentDidMount() {
    const { navigation, route } = this.props;

    let session = await AsyncStorage.getItem('session');
    this.setState({
      session: session ? JSON.parse(session) : null,
    })


    let webpageEntry = await Linking.getInitialURL();
    let service = entry['fragrance'].service;
    let url = service + 'epod/api/waba-info';
    let params = new URLSearchParams();
    // F4B4F93C59B9783FC7DF3B989256E697
    params.append('accessCode', route.params.access);
    url += ('?' + params);

    fetch(url)
      .then(response => response.json())
      .then(response => {
        console.log(response)
        if (response.wabaVipInfo.length !== 1) {
          navigation.navigate('Error')
          return;
        }
        this.setState({
          wabaVipInfo: response.wabaVipInfo,
          wabaSvInfo: response.wabaSvInfo,
        })
      })
  }

  renderItem(item) {
    const { navigation } = this.props;

    return (
      <View style={{ margin: 5, marginVertical: 10, }}>
        <View style={{ flexDirection: 'row', alignItems: 'baseline', justifyContent: 'space-between' }}>
          <Text style={{ fontFamily: 'regular', fontSize: 10, letterSpacing: 0.5 }}>{item.svId}</Text>
          <Text style={{ fontFamily: 'demi', fontSize: 18, textTransform: 'capitalize' }}>{item.svtypeName}</Text>
        </View>
        <TouchableOpacity
          style={{ marginVertical: 5, height: 110, borderRadius: 10, backgroundColor: entry['fragrance'].theme }}
          onPress={() => navigation.navigate('Voucher', { voucher: item })}>
          <Image
            style={{ flex: 1, borderRadius: 10 }}
            source={temp1}
            resizeMode='cover'
          />

          <View style={{ position: 'absolute', top: 0, left: 0, height: 30, width: 110, backgroundColor: 'black', alignItems: 'center', justifyContent: 'center', transform: [{ rotateZ: '-90deg' }, { translateX: -(110 / 2 - 30 / 2) }, { translateY: -(110 / 2 - 30 / 2) }], borderTopLeftRadius: 10, }}>
            <Text style={{ fontFamily: 'demi', color: 'white' }}>仅限一次</Text>
          </View>

          <View style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }}>
            <LinearGradient
              // Button Linear Gradient
              colors={['rgba(0,0,0,0.4)', 'transparent']}
              start={[0.9, 0.9]}
              // end={[0.1, 0.1]}
              style={{ flex: 1, alignItems: 'center', borderRadius: 10 }}>
            </LinearGradient>
          </View>

          <View style={{ position: 'absolute', bottom: 5, right: 5, height: 40, width: 40, borderRadius: 5, backgroundColor: 'white', justifyContent: 'center', alignItems: 'center' }}>
            <QRCode
              size={32}
              value={item.md5Code}
              color='black'
              logoSize={80}
            />
          </View>
        </TouchableOpacity>
        <Text style={{ fontFamily: 'regular', fontSize: 10, fontStyle: 'italic', textAlign: 'right' }}>*Terms and conditions apply. Valid till {moment(item.expiryDate).format('ll')}.</Text>

      </View>
    )
  }

  render() {
    const { navigation, route, } = this.props;
    const { wabaVipInfo, wabaSvInfo } = this.state;

    if (!wabaVipInfo) return null;

    return (
      <View style={{ backgroundColor: 'white', }}>

        <View style={{ position: 'absolute', backgroundColor: entry['fragrance'].theme, top: 120, left: 0, right: 0, height: 240, justifyContent: 'flex-end' }}>
          <Text style={{ fontFamily: 'regular', marginVertical: 5, fontSize: 10, marginHorizontal: 10, textAlign: 'center' }}>&copy; 2023 Fragrance Foodstuff Pte Ltd. All rights reserved.</Text>
        </View>


        <View style={{ height: 60, margin: 10, flexDirection: 'row' }}>
          <Image
            style={{ height: 50, width: 150, }}
            source={{
              uri: entry['fragrance'].logo
            }}
            resizeMode='contain'
          />
          <View style={{ flex: 1, marginHorizontal: 20, justifyContent: 'center', alignItems: 'flex-end' }}>
            <Text style={{ fontFamily: 'demi', fontSize: 14, textTransform: 'capitalize' }}>{wabaVipInfo[0].name} <Text style={{ color: entry['fragrance'].theme }}>{wabaVipInfo[0].vipId}</Text></Text>
            <Text style={{ fontFamily: 'regular', fontSize: 10, }}>Available Points <Text style={{ color: entry['fragrance'].theme }}>可兑换积分</Text> <Text style={{ fontFamily: 'demi' }}>{Number(wabaVipInfo[0].totalPoint || '0')}</Text></Text>
          </View>
        </View>

        <View style={{ backgroundColor: entry['fragrance'].theme1, borderRadius: 10, margin: 10, height: 240 }}>
          <Image
            style={{ flex: 1, borderRadius: 10 }}
            source={{
              uri: entry['fragrance'].wall
            }}
            resizeMode='cover'
          />
          <View style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }}>
            <LinearGradient
              // Button Linear Gradient
              colors={['black', entry['fragrance'].theme]}
              start={[0, 0]}
              end={[1, 1]}
              style={{ flex: 1, alignItems: 'center', borderRadius: 5, opacity: 0.8, }}>
            </LinearGradient>
          </View>

          <View style={{ position: 'absolute', bottom: 10, right: 10, height: 100, width: 100, borderRadius: 5, backgroundColor: 'white', justifyContent: 'center', alignItems: 'center' }}>
            <QRCode
              size={90}
              value={'F4B4F93C59B9783FC7DF3B989256E697'}
              color='black'
              logoSize={80}
            />
          </View>

          <View style={{ position: 'absolute', top: 5, left: 5, padding: 20 }}>
            <Text style={{ fontFamily: 'demi', color: 'white', fontSize: 14, letterSpacing: 1 }}>{wabaVipInfo[0].vipId}</Text>
            <Text style={{ fontFamily: 'demi', color: 'white', fontSize: 24, textTransform: 'capitalize' }}>{wabaVipInfo[0].name}</Text>
          </View>

          <View style={{ position: 'absolute', bottom: 5, left: 5, padding: 20 }}>
            <Text style={{ fontFamily: 'demi', color: 'white', fontSize: 14, textTransform: 'uppercase', letterSpacing: 1 }}>CLASS {wabaVipInfo[0].classId}</Text>
          </View>
        </View>

        <View style={{ margin: 20 }}></View>
        <Text style={{ fontFamily: 'demi', fontSize: 20, marginHorizontal: 10, }}>My Vouchers <Text style={{ color: entry['fragrance'].theme }}>我的优惠券</Text></Text>
        <Text style={{ fontFamily: 'regular', marginHorizontal: 10, }}>The perfect gift for any occasion.</Text>

        <FlatList
          style={{ flex: 1, padding: 5 }}
          data={wabaSvInfo}
          showsVerticalScrollIndicator={false}
          keyExtractor={(item, index) => index.toString()}
          renderItem={({ item }) => this.renderItem(item)}
        />

      </View>
    );
  }

}

const styles = StyleSheet.create({
});
