import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, FlatList, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Error extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  async componentDidMount() {
    let session = await AsyncStorage.getItem('session');
    this.setState({
      session: session ? JSON.parse(session) : null,
    })
  }

  render() {
    const { navigation, route, } = this.props;
    const { session } = this.state;

    return (
      <View style={{ flex: 1 }}>
        <View style={{ flex: 1, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
          <Image
            style={{ height: 50, width: 150 }}
            source={{
              uri: entry['fragrance']?.logo
            }}
            resizeMode='contain'
          />
          <Text style={{ fontFamily: 'demi', margin: 20, textAlign: 'center' }}>{route.params?.message || 'Session expired or invalid link.'}</Text>
        </View>
        {/* <TouchableOpacity style={{ marginTop: 30, marginHorizontal: 10, height: 50, backgroundColor: entry['fragrance'].theme, borderRadius: 5, justifyContent: 'center', alignItems: 'center' }}
          onPress={() => navigation.goBack()}>
          <Text style={{ fontFamily: 'demi', fontSize: 16, letterSpacing: 1, color: 'white', textTransform: 'uppercase' }}>Back 返回</Text>
        </TouchableOpacity> */}
        <Text style={{ fontFamily: 'regular', marginVertical: 5, fontSize: 10, marginHorizontal: 10, textAlign: 'center' }}>&copy; 2023 Fragrance Foodstuff Pte Ltd</Text>
      </View>
    );
  }

}

const styles = StyleSheet.create({
});
