import { Component } from 'react';
import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, Platform, Image, Linking } from 'react-native';
import { NavigationContainer, } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as Font from 'expo-font';
import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment';

import Register from './src/Register';
import Vip from './src/Vip';
import Voucher from './src/Voucher';
import Error from './src/Error';

const Stack = createNativeStackNavigator();

const linking = {
  config: {
    screens: {
      Register: {
        path: 'waba-ext/register',
      },
      Vip: {
        path: 'waba-ext/vip',
      },
      Voucher: {
        path: 'waba-ext/voucher',
      },
      Error: {
        path: '*',
      },
    },
  },
};

export default class App extends Component {

  state = {
    error: false,
  }

  async componentDidMount() {
    // debug
    // await AsyncStorage.removeItem('session')
    // ttf for native, woff for web
    await Font.loadAsync({
      'regular': require('./assets/AvenirNextLTPro-Regular.otf'),
      'demi': require('./assets/AvenirNextLTPro-Demi.otf'),
      'bold': require('./assets/AvenirNextLTPro-Bold.otf'),
      'light': require('./assets/AvenirNextLTPro-It.otf'),
    });

    let webpageEntry = await Linking.getInitialURL();
    let session = await AsyncStorage.getItem('session');
    if (session) session = JSON.parse(session);

    console.log('app mounted', webpageEntry, session?.o?.openorder)

  }

  async didFocus() { }

  render() {
    const { error } = this.state;

    if (error) return <Error />;

    return (
      <NavigationContainer
        theme={{
          dark: false,
          colors: {
            background: 'white',
          },
        }}
        linking={linking}>
        <StatusBar hidden />
        <Stack.Navigator
          screenOptions={{
            headerShown: false,
            animation: 'none',
            gestureEnabled: false,
          }}>
          <Stack.Screen name="Register" component={Register} options={Register.navigationOptions} />
          <Stack.Screen name="Vip" component={Vip} options={Vip.navigationOptions} />
          <Stack.Screen name="Voucher" component={Voucher} options={Voucher.navigationOptions} />
          <Stack.Screen name="Error" component={Error} options={Error.navigationOptions} />
        </Stack.Navigator>
      </NavigationContainer>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
